html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li,
fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section,
input[type="checkbox"] + label::after {
  display: block;
}
body { line-height: 1; }
ol, ul { list-style: none; }
blockquote, q { quotes: none; }
blockquote:before, blockquote:after, q:before, q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
code { font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace; }
html { font-size: 19px; }
h1 {
  font-size: 2.5rem;
  line-height: 5rem;
}
li, button, .create-input {
  font-size: 1rem;
  line-height: 1.5rem;
}
strong { font-weight: 600; }
#root, .modal-hide, .modal-wrapper {
  width: 100%;
  min-height: 100vh;
}
.App, #root, .modal, .group-btn, .delete-elem, .modal-wrapper, .delete-elem > span { display: flex;}
.App, #root, .modal {
  justify-content: center;
  flex-direction: column;
}
#root, .delete-elem, .modal-wrapper {
  align-items: center;
  text-align: center;
}
button, .delete-elem, .create-input, input[type="checkbox"] + label::after, input[type="checkbox"] + label::before {
  border: 1.25px solid #000000;
  border-radius: 4px;
}
.modal, input[type="checkbox"] + label { position: relative; }
.modal-hide, .modal-wrapper, input[type="checkbox"] + label::after,
input[type="checkbox"] + label::before {
  position: absolute;
}
input[type="checkbox"] + label::after, input[type="checkbox"] + label::before {
  -webkit-transition: all .2s ease-out;
  transition: all .2s ease-out;
  border-radius: 15px;
  content: "";
}
button, input[type="checkbox"] + label {
  cursor: pointer;
  outline: none;
}
#root { background-color: #ededed; }
input[type="checkbox"] { display: none; }
input[type="checkbox"] + label {
  padding-left: 50px;
  line-height: 40px;
  height: 26px;
  display: inline-block;
}
input[type="checkbox"] + label::before {
  top: 0;
  left: 0;
  vertical-align: middle;
  height: 24px;
  width: 43px;
  background-color: #e4c1be;
}
input[type="checkbox"] + label::after {
  top: 2px;
  left: 2px;
  width: 20px;
  height: 20px;
  background-color: #ffffff;
  box-shadow: 0 3px 3px rgb(140 140 140 / 0.1);
}
input[type="checkbox"]:checked + label::before { background: #96d08e; }
input[type="checkbox"]:checked + label::after { left: 20px; }
.done { text-decoration: line-through; }
.create-btn {
  padding: 0.5rem 1rem;
  align-self: center;
}
.delete-btn {
  border-radius: 50%;
  width: 26.5px;
  height: 26.5px;
  background-color: #f44336;
  line-height: 24px;
}
.delete-elem {
  justify-content: space-between;
  padding: 0.5rem;
  margin: 0.5rem 0;
}
.App { justify-content: center; }
.todo-item { align-self: stretch; }
.modal-wrapper {
  visibility: hidden;
  opacity: 0;
  justify-content: center;
  background: rgb(238 174 202);
  background: radial-gradient( circle, rgb(96 125 139 / 0.75) 0%, rgb(158 158 158 / 0.75) 100% );
  backdrop-filter: blur(0.25rem);
}
.modal {
  padding: 1.5rem 1rem 1rem;
  background-color: #ededed;
  border: 1.25px solid #000;
  z-index: 2;
}
.modal > h1 {
  line-height: 2.5rem;
  margin-top: 0;
}
.modal-hide { z-index: 1; }
.modal > * { margin: 0.5rem 0; }
.group-btn > * {
  margin: 0 5px;
  align-self: stretch;
  flex: 1 0 auto;
}
.group-btn { padding: 1rem 0; }
.active {
  transition: opacity 0.25s ease-in-out;
  opacity: 1;
  visibility: visible;
}
.create-input { padding: 0.5rem; }

@media (max-width: 320px) {
  html { font-size: 15px }
}
@media (max-width: 1915px) {
  html { font-size: 17px }
}